@keyframes blurout {
  from {
    filter: blur(0px); }
  to {
    filter: blur(3px); } }

@keyframes blurin {
  from {
    filter: blur(3px); }
  to {
    filter: blur(0px); } }

@keyframes image-blur-in {
  from {
    filter: blur(10px); }
  to {
    filter: blur(3px); } }

@keyframes image-blur-out {
  from {
    filter: blur(3px); }
  to {
    filter: blur(10px); } }

@keyframes image-blur-in-active {
  from {
    filter: blur(3px); }
  to {
    filter: blur(0px); } }

@keyframes opacity-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes opacity-out {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes opacity-in-0_5 {
  from {
    opacity: 0.5; }
  to {
    opacity: 1; } }

@keyframes opacity-out-0_5 {
  from {
    opacity: 1; }
  to {
    opacity: 0.5; } }

@keyframes loadergradient {
  0% {
    background-position: 0 0; }
  100% {
    background-position: -200% 0; } }

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(0.9);
    opacity: 0.2; }
  50% {
    -webkit-transform: scale(1);
    opacity: 1; }
  100% {
    -webkit-transform: scale(0.9);
    opacity: 0.2; } }

@keyframes pulse {
  0% {
    transform: scale(0.9);
    opacity: 0.2; }
  50% {
    transform: scale(1);
    opacity: 1; }
  100% {
    transform: scale(0.9);
    opacity: 0.2; } }

@keyframes tograyscale {
  0% {
    -webkit-filter: grayscale(0%);
    /* Ch 23+, Saf 6.0+, BB 10.0+ */
    filter: grayscale(0%);
    /* FF 35+ */ }
  100% {
    -webkit-filter: grayscale(100%);
    /* Ch 23+, Saf 6.0+, BB 10.0+ */
    filter: grayscale(100%);
    /* FF 35+ */ } }

@keyframes tocolour {
  0% {
    -webkit-filter: grayscale(100%);
    /* Ch 23+, Saf 6.0+, BB 10.0+ */
    filter: grayscale(100%);
    /* FF 35+ */ }
  100% {
    -webkit-filter: grayscale(0%);
    /* Ch 23+, Saf 6.0+, BB 10.0+ */
    filter: grayscale(0%);
    /* FF 35+ */ } }

